.waiting-room {
  width: calc(100% - 360px);
}

header {
  text-align: center;
}

.stupefy-title {
  background-image: url("/images/stupefy/stupefy.svg");
  width: 225px;
  height: 120px;
  display: inline-block;
  background-repeat: no-repeat;
}

.player-list {
  margin: auto;
  padding: 20px;
  background: mintcream;
  border-radius: 5px;
  max-width: 450px;
}

.player-list h1 {
  margin-bottom: 30px;
  position: relative;
}

.player-list h1:after {
  content: "~~~~~~~~~~~~~~~~~~~~";
  position: absolute;
  left: 0;
  bottom: -30px;
}

.waiting-player.you {
  font-weight: 900;
  color: #5f9da0;
}

.player-list .button {
  margin: 20px 0 0 0;
}

.chat-box {
  max-width: 360px;
  width: 100%;
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 30px;
  background: mintcream;
  border-left: 1px solid #2f5c5f;
  height: 100%;
}

.message-input {
  display: grid;
  grid-template-columns: 14fr 3fr;
  position: absolute;
  width: calc(100% - 60px);
  bottom: 25px;
}

.message-input .chat-input {
  min-width: initial;
  border-radius: 5px 0 0 5px;
  height: 35px;
  color: rgb(19, 19, 19);
}

.message-input .button {
  margin: 10px 0 10px 2px;
  min-width: 50px;
  border-radius: 0 5px 5px 0;
  height: 34px;
  font-size: 0.7em;
}

.message-container {
  background: #d9dcdc;
  margin: 10px 0;
  padding: 12px 15px 15px;
  border-radius: 20px;
  max-width: 90%;
}

.message-container.this-player {
  background: #c2dfe0;
  margin-left: 10%;
}

.chats {
  position: absolute;
  width: calc(100% - 60px);
  bottom: 100px;
  max-height: calc(100vh - 100px);
  overflow: scroll;
}

.heading {
  font-size: 0.7em;
  margin-bottom: 4px;
}

.heading .name {
  margin-right: 10px;
  font-weight: 700;
}

.heading .name,
.heading .time {
  display: inline-block;
}

.button.disabled,
.button.disabled:hover {
  opacity: 0.5;
  box-shadow: none;
}
