.create-room,
.join-room,
.intro {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s;
}

.text {
  color: #ffffff;
  font-weight: 900;
  font-size: 1.5em;
}

.button {
  background: #2f5d5f;
  color: white;
  display: inline-block;
  padding: 10px 20px;
  min-width: 80px;
  border-radius: 5px;
  margin: 5px;
  text-align: center;
  font-weight: 600;
  box-shadow: 0px 1px 1px #0000008c;
  cursor: pointer;
  transition: box-shadow 0.1s;
}

.button:hover {
  background: #2d5556;
  /* box-shadow: 0px 1px 1px #0000008c; */
  box-shadow: 1px 2px 0px 0px #1c383a;
  color: #ffffff;
  text-decoration: none;
}

.visible {
  opacity: 1;
  transition: opacity 0.5s linear 0.35s;
}

.hidden {
  transition: opacity 0.5s;
  opacity: 0;
  pointer-events: none;
}

.info-block {
  margin-bottom: 10px;
}

.info-block.bottom {
  margin-bottom: 0;
}

input[type="text"] {
  padding: 5px 10px;
  color: white;
  min-width: 30vw;
  width: 100%;
  border-radius: 5px;
  box-shadow: none;
  background: #2f5c5f36;
  border: none;
  margin: 10px 0px;
  border: solid 1px rgba(255, 255, 255, 0);
}

input[type="text"]:focus {
  border: solid 1px #3e5d5fa1;
  outline: none;
}

.button.create {
  margin: 5px 0;
}

input.room-input::placeholder {
  color: #3e5d5f;
}

.back-button {
  cursor: pointer;
  transition: letter-spacing 0.2s;
  text-align: right;
  position: absolute;
  top: -33px;
  left: -7px;
  width: 64px;
}

.back-button:hover {
  letter-spacing: 1px;
}
