.alert {
  position: fixed !important;
  color: #5f5f5f;
  top: 10px;
  left: 10px;
  width: calc(100% - 20px);
  background: mintcream;
  padding: 25px;
  box-shadow: 0px 1px 5px #00000054;
  z-index: 100;
  display: grid;
  grid-template-columns: 1fr 75px;
  transition: 0.4s;
}

.alert.actions {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
  max-width: 800px;
  left: 50%;
  top: calc(50% - 150px);
  /* top: 50%; */
  transform: translate(-50%, -50%);
  padding-bottom: 20px;
}

.alert.actions.subtle,
.alert.actions.resolution {
  top: 10px;
  transform: translateX(-50%);
  padding: 0 20px;
  display: initial;
}

.alert.hidden {
  opacity: 0;
  pointer-events: none;
}

.alert .message {
  padding: 4px;
}

.alert.actions .message {
  flex: 0 0 100%;
  background: #eae9e9;
  border-radius: 4px 4px 0 0;
  padding: 13px;
  border-bottom: 1px solid lightgrey;
}

.alert.actions.resolution .message,
.alert.actions.subtle .message {
  border: none;
  border-radius: 4px;
  background-color: inherit;
}

.alert.actions.subtle .message {
  display: inline-block;
  min-width: 75%;
}

.alert .a {
  cursor: pointer;
  text-align: center;
  padding: 4px 0;
  background: #eae9e9;
  border-radius: 5px;
  box-shadow: 1px 1px 1px #0000004a;
  position: relative;
  max-height: 35px;
}

.alert.actions .a {
  /* flex: 0 0 calc(50% - 0.5em); */
  flex: 1;
  margin: 20px 10px 0px 10px;
  min-width: 35%;
}

.alert.actions.subtle .a {
  padding: 4px 20px;
  margin: 7px 5px 0px 5px;
  display: inline-block;
  min-width: auto;
  font-size: 0.9em;
}

.alert .a:hover {
  color: grey !important;
  background: #e8e8e8;
  box-shadow: 1px 1px 1px #00000030;
}

.alert .a span.x {
  position: absolute;
  background-color: #b5b5b5;
  height: 13px;
  width: 13px;
  border-radius: 3px;
  border-radius: 2px;
  top: 52%;
  right: 7px;
  transform: translateY(-50%);
}

.alert .a:hover span.x {
  background-color: #d4d4d4;
}

.alert .a span.x:before {
  content: "x";
  font-weight: 900;
  font-size: 0.7em;
  color: #f3f3f3;
  position: absolute;
  top: -3px;
  right: 3px;
}
