.sidebar {
  position: relative;
  transition: 0.5s bottom;
}

.current-player {
  /* color: #ffffe6; */
  font-style: italic;
}

.current-player:after,
.current-player:before {
  content: "*";
  font-size: 0.7em;
}

.sidebar .players {
  max-height: 95px;
  overflow: scroll;
  position: relative;
}

.room-name {
  text-transform: uppercase;
  font-weight: 900;
  border-bottom: 1px dashed;
  margin-bottom: 5px;
}

.sidebar .dead {
  text-decoration: line-through;
}

.role {
  height: 70px;
  width: 125px;
  background-size: 42%;
  background-color: white;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 5px;
  margin: auto;
  margin-top: 10px;
  position: relative;
}

.role.death_eater {
  background-image: url(/images/stupefy/little_dark_mark.png);
}

.role.minister {
  background-image: url(/images/stupefy/little_m.png);
}

.role.auror {
  background-image: url(/images/stupefy/little_m2.png);
}

.role.werewolf {
  background-image: url(/images/stupefy/little_wolf.png);
}

.role:after {
  transition: 0.5s;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0;
}

.role:after:hover {
  opacity: 1;
}

.sidebar-inner .button {
  margin: 0;
  width: 100%;
  background: white;
  color: #4f4e4b;
  padding: 5px 10px;
}

.sidebar-inner .button.disabled {
  cursor: initial;
}

.sidebar-inner .button:hover {
  background: mintcream;
  color: #4f4e4b;
  box-shadow: 1px 2px 0px 0px #000000;
}

.sidebar .hidebar {
  width: 100%;
  height: 20px;
  left: 0;
  position: fixed;
}

.sidebar .hidebar .inner {
  width: 100%;
  height: 0px;
  background: #53565d;
  position: relative;
  top: 0;
  box-shadow: 1px 1px 1px #3e3b3b;
  transition: 0.5s height;
  cursor: pointer;
}

.sidebar .hidebar .inner:after {
  content: "^";
  position: absolute;
  right: 10px;
  transform: rotate(180deg);
  top: -5px;
  opacity: 0;
  transition: 0.1s;
}

.sidebar .hidebar:hover .inner:after {
  opacity: 1;
  transition: 0.2s opacity 0.2s;
}

.sidebar .hidebar:hover .inner {
  height: 20px;
}

.sidebar.hidden .hidebar .inner {
  height: 30px;
}

.sidebar.hidden .hidebar .inner:after {
  opacity: 1;
  transition: 0.2s all 0.2s;
  transform: rotate(0);
  top: 5px;
}

.sidebar.col-md-4.hidden {
  transition: 0.5s bottom;
  opacity: 1;
  pointer-events: unset;
  bottom: -270px;
}

.sidebar.col-md-4.hidden:hover {
  transition: 0.2s bottom;
  bottom: -265px;
}
