.choose-character {
  position: fixed;
  z-index: 200;
  background: cadetblue;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.choose-character .holder {
  position: absolute;
  width: 650.4px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.choose-character .title {
  font-weight: 900;
  font-size: 1.2em;
}

.choose-character .title,
.choose-character .briefing {
  padding: 7px;
}

.choose-character .briefing {
  margin-bottom: 20px;
}

.character {
  display: inline;
}
