.between-characters {
  height: 30px;
  transition: 0.2s;
  border-radius: 10px;
  padding: 10px 0;
}

.between-characters .inner-after {
  border: 2px dashed #3c757700;
  height: 100%;
  border-radius: 5px;
  transition: 0.2s;
}

.between-characters.hasclickable .inner-after {
  border: 2px dashed #3c7577;
  height: 100%;
  border-radius: 5px;
  transition: 0.2s;
}
.between-characters.hasclickable:hover {
  height: 150px;
  padding: 20px 0;
}

.between-characters.hasclickable:hover .inner-after {
  background-color: #5e9496;
  border: 1px solid #4c6f71;
  border: 2px dashed #427f82;
  box-shadow: inset 0px 0px 3px #00000030;
  border-radius: 10px;
  box-shadow: inset 0px 0px 3px #00000030, 0px 0px 11px 4px #ffc107;
  /* margin: 20px 0; */
}

.player.other-player.dead,
.player.this-player.dead .name-card,
.player.this-player.dead .character,
.player.this-player.dead .tableau {
  opacity: 0.8;
}

